    isOnline.value = false
    console.log('‰ΩøÁî®ÈªòËÆ§Âú®Á∫øÁä∂ÊÄÅ: ‰∏ãÁ∫ø')
  }
}

// ÂàùÂßãÂåñÁªÑ‰ª∂
const initializeComponent = async () => {
  if (isInitialized.value) {
    console.log('ÁªÑ‰ª∂Â∑≤ÁªèÂàùÂßãÂåñËøáÔºåË∑≥ËøáÈáçÂ§çÂàùÂßãÂåñ')
    return
