.toggle-button {
  background: linear-gradient(135deg, #7363FF 0%, #FF69DE 100%);
  border-radius: 50rpx;
  padding: 20rpx 32rpx;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 120rpx;
  position: relative;
  overflow: hidden;
  border: 2rpx solid rgba(255, 255, 255, 0.3);
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s ease;
  }
  
  &.online {
    background: linear-gradient(135deg, #F44336 0%, #FF5722 100%);
    border: none;
    
    &::before {
      left: 100%;
    }
    
    .toggle-icon {
      transform: scale(1.1);
    }
    
    .icon-play {
      transform: scale(0.8);
    }
    
    .icon-pause::before,
    .icon-pause::after {
      transform: scale(1.2);
    }
  }
  
  &:active {
    transform: scale(0.96);
    
    &::before {
      left: 100%;
    }
    
    .toggle-icon {
      transform: scale(0.9);
    }
  }
}

.toggle-icon {
  margin-right: 8rpx;
  transition: transform 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-container {
  width: 32rpx;
  height: 32rpx;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-play {
  width: 0;
  height: 0;
  border-left: 14rpx solid #FFFFFF;
  border-top: 10rpx solid transparent;
  border-bottom: 10rpx solid transparent;
  margin-left: 3rpx;
  transition: all 0.3s ease;
  filter: drop-shadow(0 2rpx 4rpx rgba(0, 0, 0, 0.2));
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    top: -10rpx;
    left: -14rpx;
    width: 0;
    height: 0;
    border-left: 14rpx solid rgba(255, 255, 255, 0.3);
    border-top: 10rpx solid transparent;
    border-bottom: 10rpx solid transparent;
    z-index: -1;
  }
}

.icon-pause {
  display: flex;
  gap: 6rpx;
  align-items: center;
  justify-content: center;
  position: relative;
}

.icon-pause::before,
.icon-pause::after {
  content: '';
  width: 5rpx;
  height: 18rpx;
  background: #FFFFFF;
  border-radius: 3rpx;
  transition: all 0.3s ease;
  box-shadow: 0 2rpx 4rpx rgba(0, 0, 0, 0.2);
  position: relative;
  
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 3rpx;
    z-index: -1;
  }
}

.toggle-text {
  color: #FFFFFF;
  font-size: 26rpx;
  font-weight: 600;
  text-shadow: 0 1rpx 2rpx rgba(0, 0, 0, 0.1);
}

